@import "../../global.scss";

.topbar {
    background-color: $backgroundColor;
    height: 80px;
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 10;

    // &:before{
    //     width: 40%;
    //     content : "";
    //      position: absolute;
    //      top:77px;
    //      right:0;
    //     border-bottom: 3px solid $goldColor;
    // }
    &:before {
        width: 70%;
        content: "";
        position: absolute;
        top: 77px;
        //  right:0;
        border-bottom: 3px solid $goldColor;
    }

    display: flex;
    justify-content: space-between;
    align-items: center;

    .topbar--left {

        flex: 4;

        @include mobile {
            flex: 9;
        }


        .topbar--logo {
            list-style: none;
            text-decoration: none;
            font-size: 20px;
            padding-left: 40px;

            @include mobile {
                font-size: 16px;
                padding-left: 20px;
            }

            span {
                color: $goldText;
            }
        }

    }

    .topbar--center {
        text-decoration: none;
        flex: 5;
        padding-right: 30px;
        font-size: 12px;

        @include mobile {
            display: none;
            flex: 0;
        }

        ul {
            display: flex;
            justify-content: space-evenly;


            a {
                text-decoration: none;
                font-weight: 300;

                &:hover {
                    color: $goldColor;
                    font-weight: 500;
                }
            }

            .color {
                color: $goldText;
            }


        }

    }

    .topbar--right {
        flex: 4;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        margin-right: 60px;

        @include mobile {
            flex: 3;
            margin-right: 25px;

        }

        .topbar--contact {

            a {

                align-items: center;
                margin-right: 20px;

                ion-icon {
                    font-size: 19px;
                    color: $goldText;
                }
            }

            @include mobile {
                display: none;
            }
        }



        @include mobile {

            .topbar--hamburger {
                display: flex;
                width: 25px;
                height: 20px;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;

                span {
                    width: 100%;
                    height: 2px;
                    background-color: $goldColor;
                    transform-origin: left;


                }

            }
        }

    }



    // .active {
    //     .topbar--hamburger {

    //         span {
    //             &:first-child {
    //                 transform: rotate(45deg);
    //             }

    //             &:nth-child(2) {
    //                 opacity: 0;
    //             }

    //             &:last-child {
    //                 transform: rotate(-45deg);
    //             }
    //         }
    //     }

    // }
}